

























































































import 'reflect-metadata'
import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import { IBlocklyToolboxItem, IBlockDefinition, IBlockLine } from '@/lib/IBlocklyHelper'
import { blocklyHelper, PredefinedBlockTypes, ColorSelectionWithNone } from '@/lib/BlocklyHelper'
import { IListItemData } from '@/lib/ICompilerRegistry'
import BlockPreview from '@/components/Blockly/BlockPreview.vue'
import CodeBlock from '@/components/CodeBlock.vue'
import BlockEditor from '@/components/Blockly/BlockEditor.vue'
import { uuid } from 'vue-uuid'
import { BlockData } from '../../lib/codeBlocksManager'

@Component({ components: { CodeBlock, BlockPreview, BlockEditor } })
export default class BlocklyCustomBlockEditor extends Vue {
    @Prop() customBlocks!: IBlockDefinition[]
    @Prop() blockDefinition!: IBlockDefinition
    @Prop({ required: true }) block!: BlockData

    indexForLine(index: number): number {
        return index + 2
    }
    iconForIndex(index: number): string {
        let nr = '' + this.indexForLine(index)
        if (index == 0) {
            nr = 'two'
        }
        return `looks_${nr}`
    }

    dismissError() {
        const err = this.block.blockly._blockErrors.find(e => e.uuid == this.blockDefinition.uuid)
        if (err != undefined) {
            err.error = ''
        }
    }

    get errorString(): string {
        const err = this.block.blockly._blockErrors.find(e => e.uuid == this.blockDefinition.uuid)
        if (err === undefined) {
            return ''
        }
        return err.error
    }

    get hasError() {
        const err = this.block.blockly._blockErrors.find(e => e.uuid == this.blockDefinition.uuid)
        return err !== undefined && err.error !== undefined && err.error.trim() != ''
    }

    get color() {
        let cl = this.blockDefinition.JSON.colour
        if (cl === undefined) {
            cl = ''
        }

        return Vue.$CodeBlock.itemForValue(this.colors, cl)
    }

    set color(v) {
        const cc = blocklyHelper.toColorCode(v.value)
        this.blockDefinition.JSON.colour = cc !== undefined ? cc : ''
    }

    get colors(): IListItemData[] {
        return ColorSelectionWithNone
    }

    get htmlColor() {
        return blocklyHelper.toHTMLColor(this.blockDefinition.JSON.colour)
    }

    get codeOptions() {
        return {
            mode: this.$CodeBlock.mimeType('xml'),
            theme: this.block.getThemeForBlock(this.codeBlock),
            lineNumbers: false,
            line: false,
            tabSize: 4,
            indentUnit: 4,
            readOnly: false,
            firstLineNumber: 1,
            gutters: []
        }
    }
    get codeString(): string {
        if (this.blockDefinition.codeString && this.blockDefinition.codeString.trim() != '') {
            return this.blockDefinition.codeString
        }
        if (this.blockDefinition.codeStub) {
            return this.blockDefinition.codeStub
        }
        return 'return ""'
    }
    get codeBlock() {
        return {
            type: 'appplication/javascript',
            content: this.codeString,
            scopeUUID: this.block.scopeUUID,
            firstLine: 2,
            hidden: false,
            readonly: false,
            static: false,
            alternativeContent: '',
            parentID: this.block.parentID,
            id: this.block.id,
            actualContent: () => {
                return this.blockDefinition.codeString
            }
        }
    }

    get staticCodeOptions() {
        return {
            mode: this.$CodeBlock.mimeType('xml'),
            theme: this.block.getThemeForBlock(this.prefixCodeBlock),
            lineNumbers: false,
            line: false,
            tabSize: 4,
            indentUnit: 4,
            readOnly: true,
            firstLineNumber: 1,
            gutters: []
        }
    }
    get prefixCodeBlock() {
        return {
            type: 'appplication/javascript',
            content: 'function(block) {',
            scopeUUID: this.block.scopeUUID,
            firstLine: 1,
            hidden: false,
            readonly: true,
            static: true,
            alternativeContent: '',
            parentID: this.block.parentID,
            id: this.block.id,
            actualContent: () => {
                return 'function(block) {'
            }
        }
    }
    codeLen: number = 0
    get postfixCodeBlock() {
        return {
            type: 'appplication/javascript',
            content: '}',
            scopeUUID: this.block.scopeUUID,
            firstLine: 2 + this.blockDefinition.codeString.split('\n').length,
            hidden: false,
            readonly: true,
            static: true,
            alternativeContent: '',
            parentID: this.block.parentID,
            id: this.block.id,
            actualContent: () => {
                return '}'
            }
        }
    }

    onBlockCodeChange() {
        if (this.codeBlock.content === this.blockDefinition.codeStub) {
            this.blockDefinition.codeString = ''
        } else {
            this.blockDefinition.codeString = this.codeBlock.content
        }
        this.blockDefinition._code = undefined
    }
}
